@import "https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:opsz,wght@9..144,700;9..144,900&display=swap";
*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-red: #fe7867;
  --primary-yellow: #fad400;
  --primary-light-cyan: #91d4c6;
  --primary-cyan: #458c7e;
  --primary-dark-cyan: #25564b;
  --primary-dark-blue: #19536b;
  --neutral-desaturated-blue: #23303e;
  --neutral-very-dark-grayish-blue: #5a636c;
  --neutral-dark-grayish-blue: #818498;
  --neutral-grayish-blue: #a7abae;
  --neutral-white: #fff;
  --ff-barlow: "Barlow", sans-serif;
  --ff-fraunces: "Fraunces", serif;
  --padding-x: 1.11rem;
  --padding-y: 1.388rem;
  --fs-900: 1.775rem;
  --fs-800: 1.445rem;
  --fs-700: 1.222rem;
  --fs-600: .777rem;
  --fs-500: .64rem;
  --fw-black: 900;
  --fw-bold: 700;
  --fw-normal: 600;
  font-size: 18px;
}

input, textarea, button, select, a {
  -webkit-tap-highlight-color: transparent;
}

header {
  padding: 0 var(--padding-x);
  padding-top: var(--padding-y);
  background-image: url("icon-arrow-down.45866d07.svg"), url("image-header.e6de50cd.jpg");
  background-position: 50% 65%, center;
  background-repeat: no-repeat, no-repeat;
  background-size: auto 90px, cover;
  grid-template: "logo nav"
                 "heading heading" 1fr
                 / 1fr 1fr;
  width: 100%;
  min-height: 784px;
  max-height: 100vh;
  display: grid;
}

header h1 {
  font-family: var(--ff-fraunces);
  font-size: var(--fs-900);
  font-weight: var(--fw-black);
  letter-spacing: 4.8px;
  text-align: center;
  text-transform: uppercase;
  color: var(--neutral-white);
  grid-area: heading;
  justify-self: center;
  margin-top: 4em;
}

header button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  outline: none;
  justify-self: end;
  position: relative;
}

.navigation {
  background-color: var(--neutral-white);
  padding: var(--padding-y) var(--padding-x);
  visibility: collapse;
  z-index: 999;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
  display: flex;
  position: absolute;
  top: 4rem;
  left: 1rem;
  right: 1rem;
}

.navigation.visible {
  visibility: visible;
}

.navigation a {
  color: var(--neutral-dark-grayish-blue);
  font-family: var(--ff-barlow);
  font-size: var(--fs-600);
  font-weight: var(--fw-normal);
  border-radius: 34px;
  outline: none;
  padding: 1em 1.5em;
  text-decoration: none;
}

.navigation a[data-primary] {
  font-family: var(--ff-fraunces);
  text-transform: uppercase;
  background-color: var(--primary-yellow);
  color: var(--neutral-desaturated-blue);
}

.navigation:after {
  content: "";
  border-style: solid;
  border-width: 0 0 24px 24px;
  border-color: transparent transparent var(--neutral-white) transparent;
  position: absolute;
  top: -24px;
  right: 0;
}

.advantage {
  grid-template-rows: auto auto;
  display: grid;
}

.advantage .background {
  aspect-ratio: 750 / 624;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
}

.advantage__transform .background {
  background-image: url("image-transform.e2a39cef.jpg");
}

.advantage__stand-out .background {
  background-image: url("image-stand-out.3c772e23.jpg");
}

.advantage .content {
  padding: calc(var(--padding-y) * 2) var(--padding-x);
  align-items: center;
  gap: var(--fs-800);
  flex-direction: column;
  display: flex;
}

.advantage .content h2 {
  color: var(--neutral-desaturated-blue);
  font-family: var(--ff-fraunces);
  font-size: var(--fs-800);
  font-weight: var(--fw-black);
  letter-spacing: -.26px;
  text-align: center;
}

.advantage .content p {
  color: var(--neutral-dark-grayish-blue);
  font-family: var(--ff-barlow);
  font-size: var(--fs-600);
  font-weight: var(--fw-normal);
  letter-spacing: -.45px;
  text-align: center;
  max-width: 40ch;
  line-height: 1.5em;
}

.advantage .content a {
  color: var(--neutral-desaturated-blue);
  font-family: var(--ff-fraunces);
  font-size: var(--fs-500);
  font-weight: var(--fw-black);
  letter-spacing: .96px;
  text-transform: uppercase;
  outline: none;
  text-decoration: none;
  position: relative;
}

.advantage .content a:hover:after, .advantage .content a:focus:after {
  opacity: 1;
}

.advantage .content a:after {
  content: "";
  opacity: .45;
  z-index: -1;
  border-radius: 12px;
  width: 100%;
  height: .5em;
  transition: all .1s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.advantage__transform .content a:after {
  background-color: var(--primary-yellow);
}

.advantage__stand-out .content a:after {
  background-color: var(--primary-red);
}

.standalone_advantage {
  aspect-ratio: 750 / 1200;
  padding: calc(var(--padding-y) * 2) var(--padding-x);
  background-repeat: no-repeat;
  background-size: contain;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  display: flex;
}

.standalone_advantage__graphic {
  background-image: url("image-graphic-design.b4d920d1.jpg");
}

.standalone_advantage__photography {
  background-image: url("image-photography.ccccde3d.jpg");
}

.standalone_advantage h2 {
  font-family: var(--ff-fraunces);
  font-weight: var(--fw-black);
  font-size: var(--fs-700);
  margin-bottom: 1em;
}

.standalone_advantage__graphic h2 {
  color: var(--primary-dark-cyan);
}

.standalone_advantage__photography h2 {
  color: var(--primary-dark-blue);
}

.standalone_advantage p {
  font-family: var(--ff-barlow);
  font-weight: var(--fw-normal);
  font-size: var(--fs-500);
  text-align: center;
  max-width: 54ch;
}

.standalone_advantage__graphic p {
  color: var(--primary-dark-cyan);
}

.standalone_advantage__photography p {
  color: var(--primary-dark-blue);
}

.opinions {
  padding: calc(var(--padding-y) * 2) calc(var(--padding-x) * 2);
  flex-direction: column;
  align-items: center;
  display: flex;
}

.opinions h2 {
  font-family: var(--ff-fraunces);
  font-weight: var(--fw-bold);
  font-size: var(--fs-500);
  letter-spacing: .25em;
  text-align: center;
  text-transform: uppercase;
  color: var(--neutral-grayish-blue);
}

.opinions ul {
  flex-direction: column;
  gap: 3rem;
  margin-top: 3rem;
  list-style-type: none;
  display: flex;
}

.opinions ul > li {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.opinions ul > li img {
  border-radius: 50%;
  width: 56px;
  height: 56px;
}

.opinions ul > li p {
  color: var(--neutral-dark-grayish-blue);
  font-family: var(--ff-barlow);
  font-size: var(--fs-500);
  font-weight: var(--fw-normal);
  text-align: center;
  max-width: 30ch;
  margin-top: .5rem;
  line-height: 1.8em;
}

.opinions ul > li h3 {
  font-family: var(--ff-fraunces);
  font-size: var(--fs-600);
  font-weight: var(--fw-black);
  margin-top: .5rem;
}

.opinions ul > li h4 {
  color: var(--neutral-dark-grayish-blue);
  font-family: var(--ff-barlow);
  font-size: .5em;
  font-weight: var(--fw-normal);
  margin-top: .25rem;
}

.endPhotos {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.endPhotos img {
  width: 100%;
  height: 100%;
}

footer {
  background-color: var(--primary-light-cyan);
  padding: calc(var(--padding-y) * 2) var(--padding-x);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

footer .logo svg {
  fill: var(--primary-dark-cyan);
}

footer nav {
  justify-content: center;
  gap: 2.5rem;
  width: 100%;
  margin-top: 1rem;
  display: flex;
}

footer nav a {
  font-family: var(--ff-barlow);
  font-size: var(--fs-500);
  font-weight: var(--fw-normal);
  color: var(--primary-cyan);
  text-decoration: none;
  transition: all .1s;
}

footer nav a:hover, footer nav a:focus {
  color: var(--neutral-white);
}

footer ul {
  gap: 1.222rem;
  margin-top: 2rem;
  list-style-type: none;
  display: flex;
}

footer ul li a svg {
  fill: var(--primary-dark-cyan);
  transition: all .1s;
}

footer ul li a svg:hover, footer ul li a svg:focus {
  fill: var(--neutral-white);
}

@media only screen and (min-width: 320px) {
